@import "variables";
@import "fonts";
@import "mixins";

.cta {
  background-color: $cta-background;

  &_blocks {
    margin: 0 auto;
    height: 200px;
    max-width: $maxWidthContainer;
    background-color: $cta-background;
    display: flex;
    flex-direction: row;
    @media (max-width: $minWidth) {
      display: block;
      height: auto;

    }
  }

}

.blocks {
  padding: 0 42px 0px 42px;
  @media (max-width: $minWidth) {
    align-items: center;
    justify-content: center;
    padding: 0 42px 0px 72px;
  }

  &_first-element {
    padding: 45px 0 0 0;
    width: 150px;
    height: 145px;
    text-align: center;

    img {
      width: 30px;
      height: 30px;

    }
    @media (max-width: $minWidth) {
      img{
        width: 80px;
        height: 80px;
      }
    }
    h3 {
      font-size: 24px;
      font-weight: 400;
      color: $cta-text;
      @media (max-width: $minWidth){
        font-size: 40px;
      }
    }

    div {
      margin-top: 20px;

      a {
        font-size: 14px;
        color: $cta-text;
        @media (max-width: $minWidth){
          font-size: 30px;
        }
      }
    }

    @media (max-width: $minWidth) {
      padding: 35px 0 0 0;
      width: 450px;
      height: auto;
      a {
        color: $cta-text;
        margin-top: 5px;

      }
    }
  }

  &_second-element {
    padding: 45px 0 0 0;
    width: 185px;
    height: 145px;
    margin-left: 140px;
    text-align: center;

    img {
      width: 30px;
      height: 30px;
    }
    @media (max-width: $minWidth) {
      img{
        width: 80px;
        height: 80px;
      }
    }
    h3 {
      font-size: 24px;
      font-weight: 400;
      color: $cta-text;
      @media (max-width: $minWidth){
        font-size: 40px;
      }
    }

    div {
      margin-top: 20px;

      a {
        font-size: 14px;
        color: $cta-text;
        @media (max-width: $minWidth){
          font-size: 30px;
        }
      }
    }

    @media (max-width: $minWidth) {
      padding: 50px 0 0 0;
      margin: -252px 0 0 385px;
      width: 450px;
      height: auto;
      a {
        margin-top: 5px;
      }
    }
  }

  &_third-element {
    padding: 45px 0 0 0;
    width: 180px;
    height: 145px;
    margin-left: 145px;
    text-align: center;

    img {
      width: 30px;
