      }
    }
    h3 {
      font-size: 24px;
      font-weight: 400;
      color: $cta-text;
      @media (max-width: $minWidth){
        font-size: 40px;
      }
    }

    div {
      margin-top: 20px;

      a {
        font-size: 14px;
        color: $cta-text;
        @media (max-width: $minWidth){
          font-size: 30px;
        }
      }
    }

    @media (max-width: $minWidth) {
      padding: 0px 0 0 0;
      margin: 15px 0 40px 0px;
      width: 450px;
      height: auto;
      a {
        margin-top: 5px;
      }
    }
  }

  &_fourth-element {
    padding: 45px 0 0 0;
